// src/pages/ProjectListings.js
import React, { useState, useEffect } from 'react';
import { collection, getDocs, query, where, orderBy } from 'firebase/firestore';
import { db } from '../firebase';
import Navigation from '../components/Navigation';
import ProjectCard from '../components/ProjectCard';

const ProjectListings = () => {
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [error, setError] = useState('');
  
  useEffect(() => {
    const fetchProjects = async () => {
      try {
        // Log which collection we're fetching from
        console.log('Fetching projects from collection: projects');
        
        // For debugging, check if the collection exists by getting its data regardless of filters
        const debugSnapshot = await getDocs(collection(db, 'projects'));
        console.log('Projects collection exists:', !debugSnapshot.empty);
        console.log('Number of documents found:', debugSnapshot.size);
        
        // Now proceed with actual filtered query
        let projectsQuery = collection(db, 'projects');
        
        if (filter !== 'all') {
          projectsQuery = query(projectsQuery, where('category', '==', filter));
        }
        
        // Only add orderBy if there are documents to sort
        if (!debugSnapshot.empty) {
          projectsQuery = query(projectsQuery, orderBy('createdAt', 'desc'));
        }
        
        const querySnapshot = await getDocs(projectsQuery);
        
        // Log the raw query results
        console.log('Query returned:', querySnapshot.size, 'documents');
        
        // Map the documents and ensure IDs are properly assigned
        const projectsList = querySnapshot.docs.map(doc => {
          const data = doc.data();
          console.log('Document ID:', doc.id, 'Document data:', data);
          return {
            id: doc.id,
            ...data
          };
        });
        
        // If there's a search term, filter projects by title or description
        const filteredProjects = searchTerm 
          ? projectsList.filter(project => 
              (project.title && project.title.toLowerCase().includes(searchTerm.toLowerCase())) || 
              (project.description && project.description.toLowerCase().includes(searchTerm.toLowerCase()))
            )
          : projectsList;
        
        console.log('Final filtered projects:', filteredProjects.length);
        setProjects(filteredProjects);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching projects:', error);
        setError(error.message);
        setLoading(false);
      }
    };
    
    fetchProjects();
  }, [filter, searchTerm]);
  
  const categories = [
    'all', 
    'Web Development', 
    'Mobile App', 
    'UI/UX Design', 
    'Marketing', 
    'Data Analysis', 
    'Content Creation'
  ];
  
  return (
    <>
      <Navigation />
      <div className="container" style={{ padding: '80px 0' }}>
        <div className="section-header">
          <div className="feature-badge">Explore Opportunities</div>
          <h2>Available Projects</h2>
          <p>Find the perfect project to enhance your skills and build your portfolio</p>
        </div>
        
        <div style={{ margin: '40px 0', display: 'flex', justifyContent: 'space-between', flexWrap: 'wrap', gap: '20px' }}>
          <div style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
            {categories.map(category => (
              <button 
                key={category}
                onClick={() => setFilter(category)}
                className={`btn ${filter === category ? 'btn-primary' : 'btn-outline'}`}
                style={{ textTransform: 'capitalize' }}
              >
                {category}
              </button>
            ))}
          </div>
          
          <div>
            <input 
              type="text"
              placeholder="Search projects..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              style={{ padding: '12px 20px', borderRadius: '50px', border: '1px solid #ddd', width: '300px' }}
            />
          </div>
        </div>
        
        {error && (
          <div style={{ backgroundColor: 'rgba(239, 68, 68, 0.1)', color: 'var(--danger)', padding: '15px', borderRadius: '8px', marginBottom: '20px' }}>
            Error: {error}
          </div>
        )}
        
        {loading ? (
          <div style={{ textAlign: 'center', padding: '50px 0' }}>Loading projects...</div>
        ) : projects.length === 0 ? (
          <div style={{ textAlign: 'center', padding: '50px 0' }}>
            No projects found. Please try a different filter or search term.
          </div>
        ) : (
          <div className="feature-grid">
            {projects.map(project => (
              <ProjectCard key={project.id} project={project} />
            ))}
          </div>
        )}
      </div>
    </>
  );
};

export default ProjectListings;